*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0 var(--size-2);
  color: var(--gray-4);
  font-size: var(--size-5);
  font-family: var(--font-sans);
  background-color: var(--gray-11);
  place-content: center;
  display: grid;
}

.pbw-title {
  font-size: var(--size-7);
  justify-content: center;
  align-items: center;
  display: flex;
}

.pbw-title * {
  margin-right: var(--size-1);
}

.pbw-title:last-child {
  margin-right: 0;
}

.pbw-logo {
  height: var(--size-7);
}

.version {
  font-weight: bold;
}

.pbw-demo {
  font-size: var(--size-3);
}

.load-bar-container {
  padding-left: var(--size-3);
  background-color: var(--gray-8);
  border-radius: var(--radius-2);
  align-items: center;
  display: flex;
}

.load-bar-container i {
  color: var(--gray-5);
}

#load-bar {
  margin: var(--size-1) 0;
  padding: var(--size-4) var(--size-3);
  color: var(--gray-2);
  font-size: var(--size-3);
  background-color: var(--gray-8);
  border: none;
  outline: none;
  width: 90%;
}

#load-bar::placeholder {
  color: var(--gray-3);
}

.btn {
  color: var(--gray-0);
  font-size: var(--size-3);
  text-align: center;
  background-color: var(--gray-9);
  border: 1px solid var(--gray-10);
  border-radius: var(--radius-2);
  cursor: pointer;
  text-decoration: none;
  transition: background-color .4s, border-color .4s;
}

.btn:hover {
  background-color: var(--gray-8);
  border-color: var(--gray-9);
}

h2 {
  font-size: var(--size-4);
}

dialog::backdrop {
  background: #00000080;
}

#pbw-dialog {
  background-color: var(--gray-12);
  width: 100%;
  max-width: 640px;
  height: 100%;
  max-height: 360px;
  padding: 0;
  overflow: visible;
}

.dialog-close-btn {
  top: calc(var(--size-3) * -1);
  z-index: 2;
  width: var(--size-7);
  height: var(--size-7);
  font-size: var(--size-4);
  text-align: center;
  border-radius: var(--radius-round);
  position: absolute;
  right: 0;
}

#examples {
  max-width: 470px;
}

#examples, #examples .category {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

#examples .category {
  width: 100%;
}

.category h2 {
  padding: var(--size-3) 0;
  background-color: var(--gray-11);
  margin: 0;
  position: sticky;
  top: 0;
}

#examples .category p {
  color: var(--gray-5);
  font-size: var(--size-3);
  text-align: left;
  margin-bottom: 0;
}

.example-btn {
  margin: var(--size-1) 0;
  padding: var(--size-5) 0;
  width: 100%;
}

